@import '../../mixin.sass'
@keyframes back
  to
    transform: none
    opacity: 1
@keyframes slide-out-blurred-top 
  0% 
    transform: translate(-50%, -50%) scaleY(1) scaleX(1)
    transform-origin: 50% 0%
    filter: blur(0)
    opacity: 1
  100%
    transform: translate(-50%, -1000px) scaleY(2) scaleX(0.2)
    transform-origin: 50% 0%
    filter: blur(40px)
    opacity: 0
@keyframes dnone
  0%
    opacity: 1
    display: block
  99%
    height: 100vh
  100%
    opacity: 0
    display: none
    height: 0
.entry
  position: fixed
  z-index: 999
  top: 0
  left: 0
  width: 100vw
  height: 100vh
  background: #ada193
